.benefits-container {
	display: flex;
	max-width: 1337px;
	width: 100%;
	margin: 0 auto;
	justify-content: space-between;
	align-items: center;
	padding: rem(66) rem(50);
	transition: 0.3s ease;
	@include r(640) {
		flex-wrap: wrap;
		background-color: rgba(#681c29, 0.5);
		//position: absolute
		bottom: 0;
		padding-top: rem(28);
		padding-bottom: rem(28); }
	&--isHover {
		transform: translateY(100%);
		@include r(640) {
			transform: translateY(0); } } }







