html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Bold.woff2") format("woff2"), url("../fonts/Oswald-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-ExtraLight.woff2") format("woff2"), url("../fonts/Oswald-ExtraLight.woff") format("woff");
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Light.woff2") format("woff2"), url("../fonts/Oswald-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

html,
body {
  font-family: Trebuchet MS, sans-serif;
  font-size: 16px;
  line-height: 1;
  height: 100%;
  width: 100%;
  font-family: "Oswald", sans-serif;
}

@media only screen and (max-width: 1170px) {
  html,
  body {
    font-size: 1vw;
  }
}

@media only screen and (max-width: 640px) {
  html,
  body {
    font-size: 16px;
  }
}

@media only screen and (max-width: 596px) {
  html,
  body {
    font-size: 14px;
  }
}

@media only screen and (max-width: 420px) {
  html,
  body {
    font-size: 12px;
  }
}

@media only screen and (max-width: 361px) {
  html,
  body {
    font-size: 10px;
  }
}

@media only screen and (max-width: 1170px) {
  main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 2;
  }
}

.page-wrapper {
  width: 100%;
  min-height: 100%;
  position: relative;
  overflow: hidden;
  background-color: #e45871;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 0;
  transition: 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.page-wrapper__overlayout {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  transition: 0.5s ease;
  opacity: 0;
}

.page-wrapper__overlayout--isHover {
  opacity: 1;
}

@media only screen and (max-width: 888px) {
  .page-wrapper {
    background-size: contain;
    background-position: 50% 50%;
  }
}

@media only screen and (max-width: 640px) {
  .page-wrapper {
    background-size: cover;
  }
}

.video-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transition: 0.3s ease;
}

.tabs-container {
  max-width: 1337px;
  width: 100%;
  margin: 0 auto;
  z-index: 3;
  align-content: stretch;
  top: 0;
  display: flex;
  align-items: center;
  transition: 0.3s ease;
  flex-wrap: wrap;
  flex-grow: 2;
  cursor: url("/img/dildo-min.png"), auto;
}

@media only screen and (max-width: 640px) {
  .tabs-container {
    justify-content: center;
    min-height: 100%;
    cursor: default;
  }
}

.page-header {
  display: flex;
  justify-content: space-between;
  max-width: 1337px;
  width: 100%;
  margin: 0 auto 5rem;
  padding-top: 9.0625rem;
  padding-left: 3.125rem;
  padding-right: 3.125rem;
  position: relative;
  transition: 0.3s ease;
}

.page-header--isHover {
  transform: translateY(-100%);
}

@media only screen and (max-width: 640px) {
  .page-header--isHover {
    transform: translateY(0);
  }
}

@media only screen and (max-width: 640px) {
  .page-header {
    padding: 2rem 2rem 0 4.25rem;
    flex-wrap: wrap;
  }
}

.page-title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 3rem;
  color: #ffccd5;
  order: 2;
}

@media only screen and (max-width: 640px) {
  .page-title {
    order: 3;
    width: 100%;
    font-size: 2rem;
    margin-top: 2.875rem;
    text-align: center;
  }
}

.page-actions {
  display: flex;
  align-items: center;
  order: 3;
}

@media only screen and (max-width: 640px) {
  .page-actions {
    order: 2;
  }
}

.lang-select {
  position: relative;
  bottom: calc(0.125rem * -1);
  width: 1.875rem;
  height: 1.375rem;
  margin-right: 0.625rem;
  cursor: pointer;
  border: 2px solid #bd3b47;
}

@media only screen and (max-width: 640px) {
  .lang-select {
    border: none;
    width: 2.875rem;
    height: 2.875rem;
    border-radius: 5px;
    margin-right: 0;
  }
}

.lang-select:before {
  content: '';
  position: absolute;
  width: 0.75rem;
  height: 0.4375rem;
  display: block;
  right: calc(1.0625rem * -1);
  top: 30%;
  background-image: url(/icons/chevron-down-min.png);
  background-size: cover;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 640px) {
  .lang-select:before {
    display: none;
  }
}

.cs-component {
  width: 100%;
  display: inline-block;
}

.cs-component__select-container {
  position: relative;
  z-index: 2;
  width: 100%;
  background-color: transparent;
}

@media only screen and (max-width: 640px) {
  .cs-component__select-container {
    background-color: #b0394e;
    padding: 0.375rem 0;
    top: 0.0625rem;
    border-radius: 0.3125rem;
  }
}

@media only screen and (max-width: 361px) {
  .cs-component__select-container {
    padding: 0.40625rem 0;
  }
}

.cs-component__select {
  width: 100%;
  cursor: pointer;
  font-family: inherit;
  font-size: 1em;
  z-index: 2;
  border: none;
  background-color: transparent;
  text-align: center;
  line-height: 1.3125rem;
  font-family: Roboto, sans-serif;
  text-transform: uppercase;
  font-size: 1.5rem;
  color: #ffccd5;
}

.cs-component__options-wrapper {
  position: absolute;
  list-style-type: none;
  width: 100%;
  height: auto;
  z-index: -3;
  transform: scaleY(0);
  transform-origin: 50% 0%;
  padding: 0;
  margin: 0;
  transition: 0.3s ease;
}

.cs-component__alt-container {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
}

@media only screen and (max-width: 640px) {
  .cs-component__alt-container {
    top: 0;
    left: 0;
  }
}

.cs-component__alt-container svg {
  position: relative;
  z-index: -2;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cs-component__options-wrapper--isActive {
  transform: scaleY(1);
}

.cs-component__option {
  font-size: 1em;
  z-index: 2;
  position: relative;
  padding: 0;
  width: 100%;
  width: 1.625rem;
  height: 1.625rem;
  transition: 0.3s ease;
}

@media only screen and (max-width: 640px) {
  .cs-component__option {
    font-size: 1.5rem;
    text-align: center;
    padding: 0.625rem 0;
    color: #ffffff;
    background-color: rgba(176, 57, 78, 0.6);
    height: 2.6875rem;
    width: 2.8125rem;
  }
}

.cs-component__option svg {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -10;
}

.cs-component__option:last-child {
  border-bottom-color: transparent;
}

.cs-component__option:hover {
  background-color: #ddd;
  cursor: pointer;
}

.service-link {
  text-transform: uppercase;
  color: #ffccd5;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-size: 1.125rem;
  transition: 0.3s ease;
  margin-left: 1.5625rem;
  position: relative;
}

@media only screen and (max-width: 640px) {
  .service-link__text-content {
    display: none;
  }
}

@media only screen and (max-width: 640px) {
  .service-link {
    background-color: #b0394e;
    padding: 0.625rem;
    font-family: Roboto, sans-serif;
    font-size: 1.5rem;
    margin-left: 0.5rem;
    border-radius: 0.3125rem;
    top: 0.125rem;
  }
}

.service-link:hover {
  color: #fff;
}

.service-link--login svg {
  position: relative;
  top: 0;
  display: none;
}

@media only screen and (max-width: 640px) {
  .service-link--login svg {
    display: block;
  }
}

@media only screen and (max-width: 640px) {
  .service-link--join {
    padding: 0.625rem 0.6875rem;
  }
}

.benefits-container {
  display: flex;
  max-width: 1337px;
  width: 100%;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  padding: 4.125rem 3.125rem;
  transition: 0.3s ease;
}

@media only screen and (max-width: 640px) {
  .benefits-container {
    flex-wrap: wrap;
    background-color: rgba(104, 28, 41, 0.5);
    bottom: 0;
    padding-top: 1.75rem;
    padding-bottom: 1.75rem;
  }
}

.benefits-container--isHover {
  transform: translateY(100%);
}

@media only screen and (max-width: 640px) {
  .benefits-container--isHover {
    transform: translateY(0);
  }
}

.tab {
  width: 25%;
  transition: 0.5s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 0;
}

@media only screen and (max-width: 640px) {
  .tab {
    width: 40%;
    margin-bottom: 1.875rem;
  }
}

.tab:before {
  content: '';
  position: absolute;
  top: 25%;
  transform: translateY(-50%);
  right: 0;
  height: 154px;
  width: 4px;
  background-color: rgba(255, 255, 255, 0.3);
  transition: background-color 0.3s ease;
}

@media only screen and (max-width: 640px) {
  .tab:before {
    display: none;
  }
}

.tab:last-child:before {
  display: none;
}

.tab:after {
  content: '';
  position: absolute;
  width: 15px;
  height: 9px;
  display: block;
  background-image: url(/icons/chevron-down-min.png);
  background-repeat: no-repeat;
  bottom: 2.5rem;
  left: 50%;
  transform: translateX(-50%);
}

@media only screen and (max-width: 640px) {
  .tab:after {
    display: none;
  }
}

.tab--is-active .tab__amount,
.tab--is-active .tab__dimention {
  transition: 0.3s ease;
  color: #ffffff;
}

.tab--is-active .tab__amount svg {
  transition: 0.3s ease;
  fill: #ffffff;
}

@media only screen and (max-width: 640px) {
  .tab--is-active .tab__price {
    color: #ff526f;
  }
}

.tab--is-active .tab__action-btn {
  opacity: 1;
}

.tab--is-active:after {
  display: none;
}

.tab--inactive .tab__amount,
.tab--inactive .tab__dimention {
  transition: 0.3s ease;
  color: rgba(255, 255, 255, 0.3);
}

.tab--inactive .tab__price {
  color: rgba(0, 0, 0, 0.4);
}

.tab--inactive .tab__amount svg {
  transition: 0.3s ease;
  fill: rgba(255, 255, 255, 0.3);
}

.tab--inactive:after {
  display: none;
}

.tab__amount {
  font-size: 8.125rem;
  margin: 0;
  position: relative;
  text-align: center;
  color: #ffffff;
  transition: 0.3s ease;
  margin-bottom: 1.25rem;
  z-index: 0;
}

.tab__dimention {
  font-size: 4.6875rem;
  margin: 0;
  position: relative;
  text-align: center;
  color: #ffffff;
  transition: 0.3s ease;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  font-weight: 600;
  margin-bottom: 1.25rem;
}

@media only screen and (max-width: 640px) {
  .tab__dimention {
    font-size: 2.5rem;
  }
}

.tab__price {
  color: #000;
  font-size: 3.125rem;
  text-align: center;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  margin-bottom: 1.5rem;
  transition: 0.3s ease;
}

@media only screen and (max-width: 640px) {
  .tab__price {
    font-size: 2.25rem;
  }
}

.tab__action-btn {
  margin: 0 auto;
  padding: 0.9375rem 3.5625rem;
  font-size: 1.5rem;
  font-weight: 300;
  background-color: #ff526f;
  color: #fff;
  border-radius: 5px;
  opacity: 0;
  cursor: pointer;
  transition: 0.5s ease;
  text-shadow: 0px 2px 1px #eb0027;
}

@media only screen and (max-width: 640px) {
  .tab__action-btn {
    padding: 1.25rem 1.875rem;
    font-size: 2.25rem;
    border-radius: 0.625rem;
  }
}

@media only screen and (max-width: 460px) {
  .tab__action-btn {
    font-size: 1.25rem;
  }
}

.tab__action-btn:hover {
  background-color: #ff6c84;
}

.benefit-item {
  display: flex;
